.card {
  display: flex;
  flex-direction: column;
  align-items: center;

  min-width: 200px;
  max-width: 200px;
  min-height: 300px;

  margin: 30px;
  padding: 20px;

  border: 1px solid rgb(243, 188, 8);
  border-radius: 5px;

  background-color: rgba(212, 160, 17, 0.719);
  cursor: pointer;

  &__img {
    width: 100%;
    border-radius: 5px;
    margin-bottom: 7px;
  }

  &__name {
    display: grid;
    grid-template-columns: 1fr 3fr;
    justify-content: space-between;
    margin: 0;
    gap: 5px;
    font-family: sans-serif;
    font-size: 20px;
    font-weight: normal;
    color: rgb(53, 39, 2);
  }

  &__text--right {
    text-align: right;
  }

  &__text--left {
    text-align: left;
    font-size: 0.9em;
    color: rgb(88, 73, 29);
  }
}